<template>
    <form class="form">
        <div class="form__field"><input v-model="user.email" v-validate="'required'" placeholder="Email" name="email" type="text" class="form__input"></div>
        <div class="form__field"><input v-model="user.password" v-validate="'required'" placeholder="Password" name="password" type="password" class="form__input"></div>
        <div class="form__field form__field--submit">
            <span class="form__span cursor-pointer">Forgot your password?</span>
           
            <button  :disabled="errors.items.length>0" @click.prevent="handleSubmit" class="form__submit bg-white text-p">Sign In</button>
        </div>
    </form>
</template>

<script>
export default {
  data() {
    return {
      user: {}
    };
  },
  methods: {
    async handleSubmit() {
      const errors = await this.$store.dispatch("signIn", this.user);
      // console.log(errors);
      if (!errors) this.$router.push("/dashboard");
      else {
        console.log("ERRORRS!!");
      }
    }
  }
};
</script>

