<template>
    <article class="slider">
        <header :class="{ 'n-pa-xs':true,'flex':true,'flex-center':true,'column':true,'slider__header':true,'bg-p':!index,'bg-s':index}">
            <img class="slider__logo" src="../assets/logo1.png" alt=""/>
            <div v-if="!index" class="flex column flex-center n-mt-xs slider__header__title">
                <p class="text-w slider__title">Welcome back</p>
                <span class="text-w slider__subtitle">Enter your sign in details below</span>
            </div>
            <div v-else class="flex column flex-center n-mt-xs slider__header__title">
                <p class="text-w slider__title">Do you want be a new student?</p>
                <span class="text-w slider__subtitle">It is free and handly takes more than 30 seconds</span>
            </div>
            
        </header>
        <main  :class="{'slider__main':true,'flex':true,'flex-center':true,'column':true,'bg-p':!index,'bg-s':index}">
            <!-- <sign-in></sign-in> -->
            <sign-in class=""  v-if="!index"/>
            <sign-up v-else/>
            <p v-if="!index" @click="changeImage(1)" class="cursor-pointer form__p">Don’t have an account? <span class="form__s">Sign up</span></p>
            <p v-if="index" @click="changeImage(2)" class="cursor-pointer form__p">Have an account? <span class="form__s">Login</span></p>
            <!-- <img class="slider__img" :src="images[index]" alt=""/> -->
        </main>
    
        
        <footer :class="{'flex':true, 'flex-center':true,'slider__footer':true,'bg-p':!index,'bg-s':index}">
            <!-- <div  @click="changeImage(1)" :class="{'cursor-pointer':true,'n-ml-xs':true,'circle':true,'slider__btn':true,'slider__btn--white':!index,'slider__btn--select':index}"></div> -->
            <!-- <div  @click="changeImage(2)" :class="{'cursor-pointer':true,'n-ml-xs':true,'circle':true,'slider__btn':true,'slider__btn--white':index,'slider__btn--select':!index}"></div> -->
        </footer>
    </article>


</template>

<script>
import SignIn from "@/components/SignIn";
import SignUp from "@/components/SignUp";

export default {
  components: { SignIn, SignUp },
  data() {
    return {
      index: 0,
      forms: ["SIGN IN", "SING UP"]
    };
  },
  methods: {
    changeImage(item) {
      switch (item) {
        case 1: {
          if (this.index == 0) this.index = this.forms.length - 1;
          else this.index--;
          break;
        }
        case 2: {
          if (this.index == this.forms.length - 1) this.index = 0;
          else this.index++;
          break;
        }
      }
    }
  }
};
</script>


